/** @format */

.button-hamburgerIcon {
  width: 30px;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 15px;
  margin-left: 14px;
  margin-right: auto;
  background-color: transparent;
}

.button-hamburgerIcon .img-hamburger {
  height: 32px;
  vertical-align: middle;
}

.button-hamburgerIcon:active {
  transform: scale(0.9);
}

.div-menu {
  margin-top: 18px;
  background-color: #14343f;
  width: 100vw;
  margin-left: calc(+100vw - 300%);
}
.div-menu ul {
  list-style: none;
  overflow: hidden;
}

.div-menu ul li {
  color: #fff;
  margin: 0px;
  padding: 0;
}

/*Links  a*/

.div-menu ul li a {
  text-decoration: none;
  color: #fff;
  display: inline-block;
  width: 100vw;
  margin-right: calc(-100vw + 100%);
  padding: 20px 32px;
  font-size: 20px;
  letter-spacing: 1.5px;
}

.div-menu ul li a:hover {
  cursor: pointer;
  text-decoration: underline #fff;
  background-color: #0b1b20;
  cursor: url("../../../assets/cursors/select.png"), auto;
}

.div-menu ul li a:active {
  background-color: #000;
}

.div-menu ul .learn-input a:hover {
  background-color: #14343f;
}

.div-menu ul .container-component {
  display: block;
  margin-left: 32px;
}

/* Pages links*/

.div-menu ul li .suport {
  margin-top: 12px;
  float: left;
}

.div-menu ul li .donate {
  font-family: "Open Sans", sans-serif;
}

/*Icons de los li*/

.div-menu ul li .icons-menu {
  padding-right: 12px;
  background-color: none;
  line-height: 0.5;
  text-align: center;
}
.div-menu ul li .morecontent .icons-menu {
  display: inline-block;
  padding-right: 24px;
}

/* separador obscuro*/

.div-menu .separador {
  height: 100vw;
  opacity: 0.5;
  background: #000;
}

/*icon pregunta*/
@media only screen and (min-width: 600px) {
  .button-hamburgerIcon {
    margin-left: auto;
    margin-right: 14px;
  }
  .div-menu {
    margin-left: calc(-100vw + 100%);
  }
}
